import React from 'react';
import PropTypes from 'prop-types';
import { includes } from 'lodash';

const style = {
  p: {
    margin: 0
  }
};

class NotLoggedIn extends React.Component {
  componentDidMount() {
    const hostname = window && window.location && window.location.hostname;
    let redirection = 'https://login.skwirrel.fr';
    if (includes(hostname, 'staging')) {
      redirection = 'https://login-staging.skwirrel.fr';
    }
    if (includes(hostname, 'preprod')) {
      redirection = 'https://login-preprod.skwirrel.fr';
    }

    window.location.assign(redirection);
  }

  render() {
    return (
      <div>
        <p style={style.p}>Redirecting...</p>
      </div>
    );
  }
}

NotLoggedIn.propTypes = {
  redirect: PropTypes.string
};

export default NotLoggedIn;



